const apiKey = process.env.HERE_MAP_API_KEY;

import icon_dizhi_b from '../assets/icon_dizhi_b.svg'

let markers = [];
const center = JSON.parse(getValueFromQuery('center'));

window.addEventListener('message', (e) => {
  try {
    const data = JSON.parse(e.data);
    console.log(data)
    markers = data.data.markers;
    if (data.signal === 'mapMarkers') {
      loadMap();
    }
  } catch (e) {
  }
})
// 参考 url http://localhost:2000/hereMapMarker.html?center=[53.14326858520508,-6.060362815856934]&markers=[{%22lat%22:53.14326858520508,%22lng%22:-6.060362815856934,%22address%22:%22unit%204%20watson%20johnson%20centre%22,%22name%22:%22Eskimo%20Pizza%20Greystones%22,%22phone%22:%2201-2878000%22},{%22lat%22:53.347740173339844,%22lng%22:-6.350144863128662,%22address%22:%221250%22,%22name%22:%22Eric%20Office%20POS%20Test%22,%22phone%22:%221250%22},{%22lat%22:53.40113830566406,%22lng%22:-6.34303617477417,%22address%22:%22EASYDISH%22,%22name%22:%221339%22,%22phone%22:%2201-6144563%22},{%22lat%22:53.40129089355469,%22lng%22:-6.342870235443115,%22address%22:%22Easy%20Dish%20Ltd,%20Cappoge,%20Dublin,%20Ireland%22,%22name%22:%22Easy%20Dish%201340%22,%22phone%22:%2201-6144563%22},{%22lat%22:53.412899017333984,%22lng%22:-8.243889808654785,%22address%22:%22wuhan%22,%22name%22:%22Chequers%20Pizza%20Test%22,%22phone%22:%220812345612%22}]


var bubble;
var ui;

// 加载地图
function loadMap () {
  const platform = new H.service.Platform({
    apikey: apiKey
  });
  const defaultLayers = platform.createDefaultLayers();

  const map = new H.Map(document.getElementById('map'),
    defaultLayers.vector.normal.map, {
    center: { lat: center[0], lng: center[1] },
    zoom: 16,
    pixelRatio: window.devicePixelRatio || 1,
    padding: { top: 50, left: 50, bottom: 50, right: 50 }
  });

  window.addEventListener('resize', () => map.getViewPort().resize());

  // const behavior = new H.mapevents.Behavior(new H.mapevents.MapEvents(map));
  ui = H.ui.UI.createDefault(map, defaultLayers);

  addMarkersAndSetViewBounds(map);
}

function addMarkersAndSetViewBounds (map) {

  const dotIcon = new H.map.Icon(icon_dizhi_b, { anchor: { x: 16, y: 16 } });
  const group = new H.map.Group();

  for (let index = 0; index < markers.length; index++) {
    const item = markers[index]
    var marker = new H.map.Marker({
      lat: item.lat,
      lng: item.lng,
    },
      { icon: dotIcon });
    marker.setData(`<section>
    <h5>${item.name}</h5>
    <div >
      <img src='assets/icon_dizhi_s.svg'/>
      <span>${item.address}</span>
    </div>
      <a href="tel:${item.phone}" style='text-decoration: none;color:rgba(15,22,33,.8);'>
      <img src='assets/icon_tel_s.svg'/>
        <span>${item.phone}</span>
      </a>
  </section>`)
    group.addObject(marker);

  }



  group.addEventListener('tap', function (evt) {
    map.setCenter(evt.target.getGeometry());
    openBubble(
      evt.target.getGeometry(), evt.target.getData());
  }, false);
  map.addObject(group);

  if (markers.length === 1) return

  map.getViewModel().setLookAtData({
    bounds: group.getBoundingBox()

  });
}


function openBubble (position, text) {
  if (!bubble) {
    bubble = new H.ui.InfoBubble(
      position,
      // The FO property holds the province name.
      { content: text });
    ui.addBubble(bubble);
  } else {
    bubble.setPosition(position);
    bubble.setContent(text);
    bubble.open();
  }
}


function getValueFromQuery (key) {
  const encodeQuery = decodeURIComponent(window.location.search);
  const match = encodeQuery.match(new RegExp('\(\\\?\|\&\)' + key + '\=\(\.\+\?\)\(\&\|\$\)'));
  if (match && match[2]) {
    return match[2]
  }
  return false
}

